@import 'variables/variables.scss';

.formWrap {
  position: absolute;
  top: 30%;
  left: 50%;

  transform: translateX(-50%);

  @media screen and (min-width: 768px) {
    top: 29%;
    // width: 608px;
    // height: 449px;
    padding: 60px 80px 40px 80px;

    background-color: $secondaryTextColor;
    box-shadow: 7px 4px 14px rgba(0, 0, 0, 0.11);
    border-radius: 40px;
  }

  @media screen and (min-width: 1280px) {
    top: 50%;
    left: 50%;

    transform: translate(-50%, -50%);
    // width: 618px;
    // height: 473px;
    padding: 60px 80px;
  }
}

.title {
  margin-bottom: 40px;
  text-align: center;

  font-size: 24px;
  line-height: 1.37em;
  font-weight: 700;
  letter-spacing: 0.04em;
  color: $mainTextColor;

  @media screen and (min-width: 768px) {
    font-weight: 500;
    font-size: 36px;
    line-height: 1.36em;
  }
}

.form {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.formGroup {
  position: relative;

  &:not(:last-of-type) {
    margin-bottom: 16px;
  }
  &:last-of-type {
    margin-bottom: 40px;
  }
}
.input {
  width: 280px;
  height: 40px;
  padding: 11px 60px 12px 14px;

  color: $mainTextColor;
  background-color: $backgroundColor;
  border: 1px solid rgba(245, 146, 86, 0.5);
  border-radius: 40px;
  font-family: 'Manrope';
  font-size: 14px;
  line-height: 1.36em;
  letter-spacing: 0.04em;

  transition: border-color 0.25s cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 0.25s cubic-bezier(0.4, 0, 0.2, 1);

  &:focus {
    background-color: rgba(0, 0, 0, 0.02);
    outline: 0;
    box-shadow: 0 0 0 0.1rem rgba(245, 146, 86, 0.5);
  }

  &::placeholder {
    color: rgba(17, 17, 17, 0.6);
  }

  @media screen and (min-width: 768px) {
    width: 448px;
    height: 52px;
    padding: 14px 70px 13px 32px;

    font-size: 18px;
    line-height: 1.39em;
  }

  @media screen and (min-width: 1280px) {
    width: 458px;
  }
}

.input:-webkit-autofill,
.input:-webkit-autofill:focus {
  transition: background-color 600000s 0s, color 600000s 0s;
}

.icon {
  position: absolute;
  top: 50%;
  right: 15px;

  color: rgba(17, 17, 17, 0.6);
  font-size: 21px;

  transform: translate(0, -50%);

  @media screen and (min-width: 768px) {
    right: 32px;
    font-size: 28px;
  }
}

.button {
  width: 280px;
  height: 40px;
  margin-bottom: 40px;

  background-color: $orangeColor;
  border: none;
  border-radius: 40px;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.36em;
  letter-spacing: 0.04em;
  color: $secondaryTextColor;
  cursor: pointer;

  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    background-color: #e7884d;
  }

  &:not(:last-of-type) {
    margin-bottom: 16px;
  }
  &:last-of-type {
    margin-bottom: 40px;
    background-color: #fff;
    color: #000;
    border: 2px solid #f59256;
    border-radius: 40px;
  }

  @media screen and (min-width: 768px) {
    width: 448px;
    height: 48px;
  }

  @media screen and (min-width: 1280px) {
    width: 458px;
  }
}

.textHint {
  font-family: 'Manrope';
  font-size: 12px;
  line-height: 1.33em;
  letter-spacing: 0.04em;
  color: rgba(17, 17, 17, 0.6);
}
.link {
  color: #3091eb;

  transition: 0.25s cubic-bezier(0.4, 0, 0.2, 1);

  &:hover {
    text-decoration: underline;
    font-weight: bold;
    letter-spacing: 0.07em;
  }
}

.errorMsg {
  position: absolute;
  margin-top: 1px;
  margin-left: 16px;

  font-size: 11px;
  color: $errorColor;

  @media screen and (min-width: 768px) {
    margin-left: 32px;
  }
}
